@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans&display=swap);
.container {
    text-align: center;
}

li {
    list-style-type: none;
}

.link {
    color: white;
}

#contact li {
    float: left;
    width: 30%
}

#contact img {
    height: 40px;
    width: 40px;
    margin-top: 10px;
}
#footer {
    color: white;
    position: fixed;
    width: 500px;
    bottom: 0;
    left: 50%;
    margin-left: -250px;
}
.shortPage {
    color: white;
    height: 100vh;
}

.shortPage h3 {
    padding-top: 3%;
    padding-left: 3%;
    font-size: 50px;
}

.shortPage h4 {
    padding-left: 3%;
    font-size: 30px;
}

.shortPage p {
    width: 70%;
    font-size: 16px;
}

/* .projectPage {
} */
.projectCard {
    height: 390px;
    margin-bottom: 50px;
    background-color: #489bc7;
    opacity: 90%;
}

.projectCard h5 {
    padding: 12px 0 0 12px;
    font-size: 36px;
    font-weight: 300;
}

.content {
    display: inline;
    float: right;
    height: 100%;
    width: 75%;
}

.cardHeader {
    display: block;
    height: 15%;
}

.dcx {
    display: block;
    height: 40%;
    margin-left: -50px;
    background-color: #2d3436;
    padding: 12px 0 0 12px;
    color: white;
}

.cardFooter {
    padding: 12px 0 0 12px;
    display: block;
    height: 45%;
}

.tags {
    word-spacing: 10px;
}

.cardFooter li {
    float: left;
    width: 10%
}

.cardFooter img {
    height: 40px;
    width: 40px;
    margin-top: 10px;
}

#kelly {
    height: 200px;
    opacity: 1;
}
* {
    font-family: 'Nunito Sans', 'Noto Sans', Roboto, sans-serif;
    font-size: 16px;
    box-sizing: border-box;
}

#background {
    background: url(/react-portfolio/static/media/iStock-680820258.86dcb84a.jpg) no-repeat center center fixed;
    background-size: cover;
}

#sidebar {
    height: 100vh;
    width: 20%;
    position: fixed;
    top: 0;
    left: 0;
    background-color: #3d5f7f;
    opacity: 90%;
    overflow-x: hidden;
    color: white;
    padding-top: 50px;
}

#main {
    margin-left: 20%;
    padding: 2%;
}
